import React, {Component} from 'react'
import Picture from './PictureCollectionItem'
import styled from 'styled-components'
import uuid from 'uuid/v1'

const collection1 = [
  {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/11.jpg',
    title: 'Soxix Dlisw'
  }, {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/15.jpg',
    title: 'Soxix Dlisw'
  }, {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/18.jpg',
    title: 'Soxix Dlisw'
  }
]

const collection2 = [
  {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/16.jpg',
    title: 'Soxix Dlisw'
  }, {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/12.jpg',
    title: 'Soxix Dlisw'
  }
]

const collection3 = [
  {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/13.jpg',
    title: 'Soxix Dlisw'
  }, {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/14.jpg',
    title: 'Soxix Dlisw'
  }, {
    url: 'https://simpleqode.bitbucket.io/touche/assets/img/17.jpg',
    title: 'Soxix Dlisw'
  }
]

export default class extends Component {
  render() {
    return (
      <Collection className="container mb-5">
        <div className="row">
          <div className="w-100">
            <p className="subtitle text-muted">Dịch vụ tang lễ</p>
            <p className="title">Sinh Phúc Thọ</p>
            <div className="collection-divider"/>
          </div>
          <div className="col p-0">
            {collection1.map(item => <Picture imgURL={item.url} key={uuid()} title={item.title}/>)}
          </div>
          <div className="col p-0">
            {collection2.map(item => <Picture imgURL={item.url} key={uuid()} title={item.title}/>)}
          </div>
          <div className="col p-0">
            {collection3.map(item => <Picture imgURL={item.url} key={uuid()} title={item.title}/>)}
          </div>
        </div>
      </Collection>
    )
  }
}

const Collection = styled.div `
  padding-top: 4rem;
  .title{
    margin-bottom: 0;
    text-align: center;
    font-size: 3rem;
    font-family: 'Playfair Display', serif;
  }
  .subtitle{
    text-align: center;
    font-family: 'Lato,sans-serif';
    margin-bottom: 0;
  }
  .collection-divider{
    border-bottom: 4px solid black;
    margin: auto;
    width: 3rem;
    margin-bottom: 3rem;
  }
`


